<template>
    <div ref="fireworksContainer" class="fireworks-container">
    </div>
    <div class="btn-music">
        <button @click="play">Click to play a sound</button>
    </div>
</template>

<script>
// import { onMounted } from 'vue'
import { Fireworks } from 'fireworks-js'
import happyBirthday from '@/assets/happy-birthday.mp3'
import { useSound } from '@vueuse/sound';
export default {
    name: 'FireworksDisplay',
    setup() {
        const { play } = useSound(happyBirthday)
        return {
            play
        }

    },
    mounted() {
        const container = this.$refs.fireworksContainer;
        const fireworks = new Fireworks(container, {
            autoresize: true,
            opacity: 0.5,
            acceleration: 1.05,
            friction: 0.97,
            gravity: 1.5,
            particles: 50,
            traceLength: 3,
            traceSpeed: 10,
            explosion: 5,
            intensity: 30,
            flickering: 50,
            lineStyle: 'round',
            hue: {
                min: 0,
                max: 360
            },
            delay: {
                min: 30,
                max: 60
            },
            rocketsPoint: {
                min: 50,
                max: 50
            },
            lineWidth: {
                explosion: {
                    min: 1,
                    max: 3
                },
                trace: {
                    min: 1,
                    max: 2
                }
            },
            brightness: {
                min: 50,
                max: 80
            },
            decay: {
                min: 0.015,
                max: 0.03
            },
            mouse: {
                click: false,
                move: false,
                max: 1
            },

        })
        console.log('firework')
        fireworks.start();
        this.play()
    },
    methods: {
       
    }
}
</script>

<style scoped>
.fireworks-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: black;
    background-image: url('@/assets/moon.jpg');
    background-size: 500px 500px;
    background-repeat: no-repeat;
}

.btn-music {
    position: absolute;
    background-color: black;
    color: white;
    bottom: 10px;
    right: 0;
    z-index: 2;
}
</style>